.NavBar{
    width: 100%;
    height: 60px;
    background-color: #D5DBDB ;
    direction: rtl;
    display: flex;
}
.NavBar .LiftSide{
    width: 70%;
    display: flex;
    justify-items: center;
    justify-content: center;
}
.NavBar .LiftSide .Links{
    margin: 10px;
}
.NavBar .LiftSide #hidden{
    display: flex;
}
.NavBar .LiftSide .Links .ListLinks {
    display: flex;
   list-style-type: none;
}
.NavBar .LiftSide .Links .ListLinks li a{
    text-decoration: none;
    font-size: 20px;
    color:black;
    margin: 10px;
}
.NavBar .LiftSide .Menu{
    display: none;
}
.NavBar .RightSide{
    width: 30%;
    display: flex;
    justify-content: flex-end;
}
.NavBar .RightSide .Title{
    display: none;
}

.NavBar .RightSide .Sign{
    display: flex;
    justify-content: flex-end;
 
}
.NavBar .RightSide .Search {
    /* background: coral; */
}

.NavBar .RightSide .Sign button{
   /* background: chartreuse; */
   justify-content: flex-end;

}
.NavBar .RightSide .SearchIconField {
    display: none;
}


@media  screen and (max-width:1350px) {
    .NavBar .LiftSide .Links a{
        font-size: 15px;
        font-family: Arial, Helvetica, sans-serif;
    }
   
    
}
@media  screen and (max-width:1050px) {
    .NavBar .LiftSide .Links a{
        font-size: 12px;
        font-family: Arial, Helvetica, sans-serif;
    }
}
@media  screen and (max-width:900px) {
    .NavBar .LiftSide .Links {
       display: none;
    }
    .NavBar .LiftSide #hidden .ListLinks a{
        font-size: 20px;

    }
    .NavBar .LiftSide #hidden .ListLinks li{
        margin: 10px;
    }
    .NavBar .LiftSide {
        width: 7%;
    }
    .NavBar .LiftSide .Menu{
        display: flex;
        justify-content: flex-start;
    }
    .NavBar .LiftSide #hidden .ListLinks{
        position: absolute;
        left: 0px;
        right: 7px;
        margin-top: 50px;
        height: 100%;
        width: 60%;
        background-color:#D5DBDB;
        display: flex;
        flex-direction: column;
        z-index: 9999;

    }
    .NavBar .RightSide {
        width: 93%;
        display: flex;
        /* background-color: black; */
    }
    
   
    .NavBar .RightSide .SearchIconField {
        display: flex;
        width: 50%;
    }
    .NavBar .RightSide .SearchIconField button {
        width: 30px;
        height: 30px;
        margin: 10px;
        margin-top: 20px;
        margin-left: 0px;
    }
    
    .NavBar .RightSide .Title {
        display: flex;
        justify-content: center;
        margin-top: 20px;
        font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
        width: 50%;
    }
    .NavBar .RightSide .Title img{
        width: 20px;
        height: 20px;
    }
    .NavBar .RightSide .Title a{
        text-decoration: none;
        font-size: 25px;
        color: black;
    }
    
    .NavBar .RightSide .Search {
        display: none;
    }
    .NavBar .RightSide #hidden {
        display: flex;
        margin-top: 60px;
        position: absolute;
        z-index: 9999;
    }
    .NavBar .RightSide #hidden input {
        z-index: 9999;
    }
       
       

  
}


